import React from 'react';
import styled from "styled-components";
import "../../App.css"
import * as Scroll from "react-scroll"

const UL = styled.ul`
    list-style: none;
    display: flex;
    font-size: 20px;
`;

const LI = styled.li`
    color: white;
    @media screen and (max-width: 780px){
    display:none;
    }
    border-right: 1px solid white;
    border-left: 1px solid white;
    margin-right: 10px;
    margin-left: 10px;
    & a {
        cursor: pointer;
        color:white;
        text-decoration: none;
        padding-left: 10px;
        padding-right: 10px;
    }
    & a:hover{
        border-bottom: 1px solid white;
        color: rgba(255,255,255,0.7);
    }
`
const scrollToProfile = () => {
    Scroll.scroller.scrollTo("profile",{
        duration: 800,
        delay: 0,
        smooth: 'easeInOutQuart'
    })
}
const scrollToSkill = () => {
    Scroll.scroller.scrollTo("skill",{
        duration: 800,
        delay: 0,
        smooth: 'easeInOutQuart'
    })
}
const scrollToWorks = () => {
    Scroll.scroller.scrollTo("works",{
        duration: 800,
        delay: 0,
        smooth: 'easeInOutQuart'
    })
}
const scrollToContact = () => {
    Scroll.scroller.scrollTo("contact",{
        duration: 800,
        delay: 0,
        smooth: 'easeInOutQuart'
    })
}





function HeaderNav() {
    return(
        <UL>
            <LI onClick={scrollToProfile}><a>PROFILE</a></LI>
            <LI onClick={scrollToSkill}><a>SKILL</a></LI>
            <LI onClick={scrollToWorks}><a>WORKS</a></LI>
            <LI onClick={scrollToContact}><a>CONTACT</a></LI>
        </UL>
    )
}

export default HeaderNav;