import React from 'react'
import "../../App.css"
import * as Scroll from "react-scroll"

const scrollToHome = () => {
    Scroll.scroller.scrollTo("app",{
        duration: 800,
        delay: 0,
        smooth: 'easeInOutQuart'
    })
  }

const scrollToProfile = () => {
    Scroll.scroller.scrollTo("profile",{
        duration: 800,
        delay: 0,
        smooth: 'easeInOutQuart'
    })
}
const scrollToSkill = () => {
    Scroll.scroller.scrollTo("skill",{
        duration: 800,
        delay: 0,
        smooth: 'easeInOutQuart'
    })
}
const scrollToWorks = () => {
    Scroll.scroller.scrollTo("works",{
        duration: 800,
        delay: 0,
        smooth: 'easeInOutQuart'
    })
}
const scrollToContact = () => {
    Scroll.scroller.scrollTo("contact",{
        duration: 800,
        delay: 0,
        smooth: 'easeInOutQuart'
    })
}
// menuのa要素押してnavTglを閉じる
function clickHome() {
    scrollToHome();
    document.getElementById("navTgl").checked = false;
}
function clickProfile() {
    scrollToProfile();
    document.getElementById("navTgl").checked = false;
}
function clickSkill() {
    scrollToSkill();
    document.getElementById("navTgl").checked = false;
}
function clickWorks() {
    scrollToWorks();
    document.getElementById("navTgl").checked = false;
}

function clickContact() {
    scrollToContact();
    document.getElementById("navTgl").checked = false;
}

export default function DrawerNav() {
    return (
        <body>
            <input type="checkbox" id="navTgl" />
            <label for="navTgl" className="open" id="open"><span></span></label>
            <label for="navTgl" className="close" id="close"></label>
	        <nav className="menu">
		        <ul>
			        <li><a onClick={clickHome}>HOME</a></li>
			        <li><a onClick={clickProfile}>PROFILE</a></li>
			        <li><a onClick={clickSkill}>SKILL</a></li>
			        <li><a onClick={clickWorks}>WORKS</a></li>
			        <li><a onClick={clickContact}>CONTACT</a></li>
                </ul>
            </nav>
        </body>
    )
};
