import React from 'react'
import Icon from "../Image/TwitterIcon.PNG"
import ContactSupportIcon from '@material-ui/icons/ContactSupport';
import "../../App.css"


export default function Contact() {
    return (
        <div id="contact" className="contact-wrapper">
            <div className="contact-container">
            <h2>C<ContactSupportIcon className="profileicon"/>NTACT</h2>
                <div className="profile-context">
                    <img className="twittericon" src={Icon} alt="twittericon"></img>
                    <p>ご連絡はTWITTERのDMの方からよろしくお願い致します。</p>
                    <p>↓↓↓</p>
                    <a href="https://twitter.com/shu1928vs">TWITTER</a>
                </div>
            </div>
        </div>
    )
}
