import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import "../../App.css"
import ShujisPortfolio from"../Image/Shuji's-Portfolio.png"
import clock from"../Image/clock.png"
import WeatherSpa from"../Image/Weather-SPA-App.png"
import QuizApp from "../Image/Quiz-App.png"
import TodoApp from"../Image/Todo-App.png"
import createAcount from "../Image/create-acount.png"


const useStyles = makeStyles((theme) => ({
  root: {
    width: "32%",
    minHeight: 320,
    marginRight: "10px",
    marginBottom: "10px",
    [theme.breakpoints.down("850")]: {
      width: "48%",
    },
    [theme.breakpoints.down("701")]: {
      width: "100%",
    },
  },
  cardContainer: {
    display: "flex",
    flexWrap: "wrap",
    width: "90%",
    margin: "0 auto",
    paddingBottom: "10px",
  },
  media: {
    height: 165,
  },
}));

export default function WorkCard() {
  const classes = useStyles();

  return (
    <div className={classes.cardContainer}>
    <Card xs={12} className={classes.root}>
      <CardActionArea href="https://ansonys.github.io/Shuji-Portfolio/">
        <CardMedia
          className={classes.media}
          image={ShujisPortfolio}
          title="SHUJI'S PORTFOLIO"
        />
        <CardContent>
        <Typography gutterBottom variant="h5" component="h6">
            このサイトです
          </Typography>
          <Typography variant="body2" color="textSecondary" component="p">
            React,Material-UIを初めて使い制作したサイトです。
            CSS/React/Material-UI
          </Typography>
        </CardContent>
      </CardActionArea>
      <CardActions>
        <Button size="small" color="primary" href="https://ansonys.github.io/Shuji-Portfolio/">
          もっと見る
        </Button>
      </CardActions>
    </Card>

    <Card className={classes.root}>
      <CardActionArea href="https://ansonys.github.io/clockpf/">
        <CardMedia
          className={classes.media}
          image={clock}
          title="アナログ時計"
        />
        <CardContent>
          <Typography gutterBottom variant="h5" component="h6">
            アナログ時計です
          </Typography>
          <Typography variant="body2" color="textSecondary" component="p">
            1秒ごとに秒針が移動し、現在時刻を表します。HTML/CSS/JavaScript
          </Typography>
        </CardContent>
      </CardActionArea>
      <CardActions>
        <Button size="small" color="primary" href="https://ansonys.github.io/clockpf/">
          もっと見る
        </Button>
      </CardActions>
    </Card>

    <Card className={classes.root}>
      <CardActionArea href="https://ansonys.github.io/Weather-SPA-App/">
        <CardMedia
          className={classes.media}
          image={WeatherSpa}
          title="天気予報アプリ"
        />
        <CardContent>
          <Typography gutterBottom variant="h5" component="h6">
            天気予報アプリです
          </Typography>
          <Typography variant="body2" color="textSecondary" component="p">
            SPAに対応しており、テキスト欄に市を入力し、現在の天気や気温を表示します。React/css/react-dom/react-router-dom/Ajax
          </Typography>
        </CardContent>
      </CardActionArea>
      <CardActions>
        <Button size="small" color="primary" href="https://ansonys.github.io/Weather-SPA-App/
        
">
          もっと見る
        </Button>
      </CardActions>
    </Card>

    <Card className={classes.root}>
      <CardActionArea href="https://ansonys.github.io/quizApp/">
        <CardMedia
          className={classes.media}
          image={QuizApp}
          title="Contemplative Reptile"
        />
        <CardContent>
          <Typography gutterBottom variant="h5" component="h6">
            クイズアプリ
          </Typography>
          <Typography variant="body2" color="textSecondary" component="p">
            5問問題があり、正答数に応じて、正答数と正解率を表示します。(個人的に好きなアニメとゲームの問題です。)HTML/CSS/JavaScript
          </Typography>
        </CardContent>
      </CardActionArea>
      <CardActions>
        <Button size="small" color="primary" href="https://ansonys.github.io/quizApp/
">
          もっと見る
        </Button>
      </CardActions>
    </Card>

    <Card className={classes.root}>
      <CardActionArea href="https://ansonys.github.io/todoApp/">
        <CardMedia
          className={classes.media}
          image={TodoApp}
          title="Contemplative Reptile"
        />
        <CardContent>
        <Typography gutterBottom variant="h5" component="h6">
            TODOアプリ
          </Typography>
          <Typography variant="body2" color="textSecondary" component="p">
            TODOを入力し、ENTERでTODOを追加、左クリックでTODOのマーク、右クリックでTODOを削除します。HTML/CSS/javaScript
          </Typography>
        </CardContent>
      </CardActionArea>
      <CardActions>
        <Button size="small" color="primary" href="https://ansonys.github.io/todoApp/
">
          もっと見る
        </Button>
      </CardActions>
    </Card>

    <Card className={classes.root}>
      <CardActionArea href="https://ansonys.github.io/create-acount/">
        <CardMedia
          className={classes.media}
          image={createAcount}
          title="Contemplative Reptile"
        />
        <CardContent>
        <Typography gutterBottom variant="h5" component="h6">
            アカウント作成フロント
          </Typography>
          <Typography variant="body2" color="textSecondary" component="p">
            アカウント作成のフロント部分です。不備があればinput部分に赤いマークがつき、不備がなければ緑のマークになります。HTMl/CSS/JavaScript
          </Typography>
        </CardContent>
      </CardActionArea>
      <CardActions>
        <Button size="small" color="primary"　href="https://ansonys.github.io/create-acount/">
          もっと見る
        </Button>
      </CardActions>
    </Card>
    </div>
  );
}
