import { FullscreenExit, InsertLinkRounded } from '@material-ui/icons';
import React from 'react';
import styled from "styled-components";
import "../App.css"
import * as Scroll from "react-scroll"

const FooterList = styled.footer`
    width: 100%;
    background-color: rgba(0,0,0,0.9);
    font-size: 20px;
    display: flex;
    justify-content: center;
    @media screen and (min-width: 780px){
        height: 100px;
    }
`

const UL = styled.ul`
    text-align: center;
    @media screen and (min-width: 780px){
        display: flex;
    }
    @media screen and (max-width: 780px){
        margin-left: -40px;    
    }
`;

const LI = styled.li`
    & a:hover{
        border-bottom: 1px solid white;
    }
    & a {
        cursor: pointer;
        color: white;
        text-decoration: none;
        padding-left: 10px;
        padding-right: 10px;
    }
    & a:hover{
        color: rgba(255,255,255,0.7);
    }
`

const style = {
    display: "flex",
    justifyContent: "center",
    alignItem: "center",
    backgroundColor: "rgba(0,0,0,0.5)",   
}

const scrollToHome = () => {
    Scroll.scroller.scrollTo("app",{
        duration: 800,
        delay: 0,
        smooth: 'easeInOutQuart'
    })
}

const scrollToProfile = () => {
    Scroll.scroller.scrollTo("profile",{
        duration: 800,
        delay: 0,
        smooth: 'easeInOutQuart'
    })
}
const scrollToSkill = () => {
    Scroll.scroller.scrollTo("skill",{
        duration: 800,
        delay: 0,
        smooth: 'easeInOutQuart'
    })
}
const scrollToWorks = () => {
    Scroll.scroller.scrollTo("works",{
        duration: 800,
        delay: 0,
        smooth: 'easeInOutQuart'
    })
}
const scrollToContact = () => {
    Scroll.scroller.scrollTo("contact",{
        duration: 800,
        delay: 0,
        smooth: 'easeInOutQuart'
    })
}

function Footer() {
    return (
        <>
        <FooterList>
            <UL>
                <LI><a onClick={scrollToHome}>HOME</a></LI>
                <LI><a onClick={scrollToProfile}>PROFILE</a></LI>
                <LI><a onClick={scrollToSkill}>SKILL</a></LI>
                <LI><a onClick={scrollToWorks}>WORKS</a></LI>
                <LI><a onClick={scrollToContact}>CONTACT</a></LI>
            </UL>
        </FooterList>
            <small style={style}>©2020 SHUJI</small>
        </>
    )
}

export default Footer;
